<template>
  <div class="takslist">
    <Top gorouter="/index"></Top>
    <div class="workstatus">
      <p class="title">{{statusname}}</p>
    </div>
    <div class="tasklist">
      <ul>
        <li v-for="item in tasklist" :key="item.id">
          <router-link to="/tasklist_add" v-if="status" class="box">待完成的农事任务
            <!-- <img class="abs-status" :src="{myimg}"> -->
            <img class="abs-status" src="../../assets/public/plus.png">
            <p class="task-title">{{item.title}}</p>
            <p class="task-time">发布时间：{{item.time}}</p>
          </router-link>
          <div class="box" v-else>已经完成的农事任务
            <img class="abs-status" src="../../assets/public/choose.png">
            <p class="task-title">{{item.title}}</p>
            <p class="task-time">发布时间：{{item.time}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import Top from "@/components/public/top";
// import plusimg from "../../assets/public/plus.png";
// import chooseimg from "../../assets/public/choose.png";
export default {
  data() {
    return {
      status: true,
      statusname: "",
      //   myimg: "",
      tasklist: [
        { id: 1, title: "2月18日前完成播种任务", time: "2019年2月12日" },
        { id: 2, title: "2月18日前完成浇水任务", time: "2019年2月12日" }
      ]
    };
  },
  created() {
    console.log(this.status);
    let s, n;
    if (this.$route.params.statusname == "working") {
      s = "待完成的农事任务";
      //   n = chooseimg;
      this.status = true;
    } else {
      console.log(222222);
      s = "已经完成的农事任务";
      //   n = plusimg;
      this.status = false;
    }
    this.statusname = s;
    console.log(this.status);
    // this.myimg = n;
    //请求数据填充到tasklist
  },
  methods: {},
  components: {
    Top
  }
};
</script>
<style>
.workstatus {
  background: #0078ad;
  padding: 10px 0;
  color: white;
  text-align: center;
}
.workstatus .title {
  font-size: 10px;
  line-height: 10px;
}
.tasklist,
.userout {
  padding: 10px;
}
.tasklist ul li {
  margin-bottom: 10px;
}
.tasklist ul li .box {
  display: block;
  text-align: left;
  padding: 10px 25px 10px 50px;
  background: white;
  font-size: 10px;
  line-height: 1rem;
  position: relative;
}
.tasklist .abs-status {
  position: absolute;
  left: 10px;
  top: 15px;
  width: auto;
  height: 30px;
}
</style>


