<template>
  <div class="login">
    <h1 class="company-name">{{company_name}}</h1>
    <div class="login-form">
      <van-cell-group class="login-inputs">
        <van-field
          v-model="form_data.username"
          required
          clearable
          label="用户名"
          right-icon="question-o"
          placeholder="请输入用户名"
          @click-right-icon="$toast('如果没有帐号请联系管理员')"
        />
        <van-field
          v-model="form_data.password"
          type="password"
          label="密码"
          placeholder="请输入密码"
          required
        />
      </van-cell-group>
      <van-row type="flex" class="login-status" justify="space-between">
        <van-col span="7">
          <van-checkbox v-model="form_data.automatic_login">自动登录</van-checkbox>
        </van-col>
        <van-col span="7">
          <router-link to="/forget" class="forget">忘记密码</router-link>
        </van-col>
      </van-row>
    </div>
    <van-button type="info" size="large" class="login-button" @click="fnlogin">登录</van-button>
    <!-- <van-row type="flex" justify="center" class="fixed-register">
      <van-col span="6">
        <router-link to="/register">注册</router-link>
      </van-col>
    </van-row>-->
  </div>
</template>

<script>
export default {
  data() {
    return {
      company_name: "湖南机智物联欢迎您",
      form_data: {
        username: "",
        password: "",
        automatic_login: false
      }
    };
  },
  methods: {
    fnlogin() {
      // if(!this.form_data.username){
      //     this.$toast.fail('用户名不为空');
      //     return
      // }
      // if(!this.form_data.password){
      //     this.$toast.fail('密码不能为空');
      //       return
      // }
      // 调接口
      this.$toast.success("登录成功");
      setTimeout(() => {
        this.$router.push("/index");
      }, 1000);
    }
  },
  mounted() {}
};
</script>
<style>

.login {
  padding: 10px;
}
.login .company-name {
  margin: 50px 0 80px;
  color: #0078ad;
}
.login .login-status {
  font-size: 14px;
  margin: 10px 0;
}
.login .forget {
  color: #323233;
  margin-left: 10px;
  display: inline-block;
  vertical-align: middle;
  line-height: 20px;
}
.login-button {
  margin-top: 50px;
}
.fixed-register {
  position: fixed;
  bottom: 5px;
  left: 0;
  right: 0;
}
.fixed-register a {
  display: block;
  padding: 5px 10px;
}
</style>


